.page-accomodation {
  margin-top: 38px;
  background-color: #ffffff;
}

@media (min-width: @desktop-width) {
  .page-accomodation {
    margin: 0;
    box-shadow: 6px 0 4px -4px #ffffff, -6px 0 4px -4px #ffffff;
  }
}

.page-accomodation__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-accomodation__notice {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}

.page-accomodation__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
}

.page-accomodation__link {
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}

.page-accomodation__map {
  display: flex;
  height: 350px;
  background: url("../../img/map.jpg") no-repeat;
}
